import React from 'react';
import {TokenUtil} from "../util/TokenUtil";
import {Navigate} from "react-router-dom";
import * as userReducer from "../redux/users/user.reducer";
import {useSelector} from "react-redux";
import {RootState} from "../redux/store";

const AdminRoute: React.FC<any> = ({children}: any) => {

    // get user data from redux
    const userReduxState: userReducer.InitialState = useSelector((state: RootState) => {
        return state[userReducer.userFeatureKey];
    });

    const {user} = userReduxState;

    const isLoggedInUser = TokenUtil.isLoggedIn();
    const isAdminUser = user && user.isAdmin;
    return (isLoggedInUser && isAdminUser) ? children : <>
        <Navigate to="/"/>
    </>;
}
export default AdminRoute;
