import React from 'react';
import {IContactView} from "../models/IContactView";
import {Link} from "react-router-dom";
import {IGroupView} from "../models/IGroupView";

interface IProps {
    contact: IContactView;
    group: IGroupView;
}

const ViewContactCard: React.FC<IProps> = (props) => {
    const {contact, group} = props;
    return (
        <>
            <div className="container mt-3">
                <div className="row" id="contact-row">
                    <div className="col-sm-3">
                        <img alt=""
                             className="img-fluid w-75 rounded-circle shadow-lg"
                             src={contact.imageUrl}/>
                    </div>
                    <div className="col-sm-8 offset-1">
                        <ul className="list-group">
                            <li className="list-group-item">
                                Name : <span className="fw-bold">{contact.name}</span>
                            </li>
                            <li className="list-group-item">
                                Email : <span className="fw-bold">{contact.email}</span>
                            </li>
                            <li className="list-group-item">
                                Mobile : <span className="fw-bold">{contact.mobile}</span>
                            </li>
                            <li className="list-group-item">
                                Company : <span className="fw-bold">{contact.company}</span>
                            </li>
                            <li className="list-group-item">
                                Title : <span className="fw-bold">{contact.title}</span>
                            </li>
                            <li className="list-group-item">
                                Group : <span className="fw-bold">{group.name}</span>
                            </li>
                        </ul>
                    </div>
                </div>
                <div className="row">
                    <div className="col">
                        <Link className="btn btn-warning" to="/contacts/display">
                            <i className="bi bi-arrow-left-circle"></i> Back</Link>
                    </div>
                </div>
            </div>
        </>
    );
}
export default ViewContactCard;
