import React from 'react';
import {Link} from 'react-router-dom';
import {TokenUtil} from "../util/TokenUtil";
import * as userReducer from "../redux/users/user.reducer";
import {useSelector} from "react-redux";
import {AppDispatch, RootState, useAppDispatch} from "../redux/store";
import {logOutUserAction} from "../redux/users/user.reducer";

interface IProps {
    heading: string;
}

const NavBar: React.FC<IProps> = (props) => {
    const dispatch: AppDispatch = useAppDispatch();

    // get user state from redux
    const userState: userReducer.InitialState = useSelector((state: RootState) => {
        return state[userReducer.userFeatureKey];
    });

    const {user: userReduxState} = userState;

    const clickLogOff = () => {
        dispatch({
            type: `${logOutUserAction}`,
            payload: {}
        });
    };

    return (
        <>
            <nav className="navbar navbar-dark bg-dark navbar-expand-sm">
                <div className="container">
                    <Link to="/" className="navbar-brand">
                        <i className="bi bi-phone"></i> {props.heading.split(" ")[0]}
                        <span className="text-warning"> {props.heading.split(" ")[1]}</span>
                    </Link>
                    <div className="navbar-collapse collapse">
                        {
                            TokenUtil.isLoggedIn() && <ul className="navbar-nav me-auto">
                                <li className="nav-item">
                                    <Link to="/contacts/display" className="nav-link">Display</Link>
                                </li>
                                {userReduxState.isAdmin && <li className="nav-item">
                                    <Link to="/contacts/admin" className="nav-link">Admin</Link>
                                </li>}
                            </ul>
                        }

                        <ul className="navbar-nav ms-auto">
                            {
                                TokenUtil.isLoggedIn() ? <>
                                    {
                                        userReduxState && Object.keys(userReduxState).length > 0 &&
                                        <li className="nav-item">
                                            <Link to="/" className="nav-link">
                                                <img src={userReduxState.imageUrl} width={25} height={25}
                                                     className="rounded-circle"
                                                     alt=""/> {userReduxState.username}
                                            </Link>
                                        </li>
                                    }
                                    <li className="nav-item">
                                        <Link to="/" className="nav-link"><i className="bi bi-power"
                                                                             onClick={clickLogOff}></i></Link>
                                    </li>
                                </> : <li className="nav-item">
                                    <Link to="/users/login" className="nav-link">Login</Link>
                                </li>
                            }
                        </ul>
                    </div>
                </div>
            </nav>
        </>
    );
}
export default NavBar;
