import React, {useEffect} from 'react';
import NavBar from "../../../layout/NavBar";
import LayoutHeading from "../../../layout/LayoutHeading";
import {useParams} from "react-router-dom";
import SpinnerUI from "../../../layout/SpinnerUI";
import ErrorMessage from "../../../layout/ErrorMessage";
import ViewContactCard from "../components/ViewContactCard";
import * as contactActions from "../../../redux/contacts/contact.actions";
import {AppDispatch, RootState, useAppDispatch} from "../../../redux/store";
import * as contactReducer from "../../../redux/contacts/contact.reducer";
import {useSelector} from "react-redux";

const ViewContact: React.FC = () => {
    const {contactId} = useParams();
    const dispatch: AppDispatch = useAppDispatch();

    // get contact State from redux
    const contactReduxState: contactReducer.InitialState = useSelector((state: RootState) => {
        return state[contactReducer.contactFeatureKey];
    });

    const {loading, errorMessage, contact, group} = contactReduxState;

    useEffect(() => {
        if (contactId) {
            getContactInfoFromServer(contactId);
        }
    }, [contactId]);

    useEffect(() => {
        if (contact && Object.keys(contact).length > 0) {
            if (contact.groupId) {
                getGroupInfoFromServer(contact.groupId);
            }
        }
    }, [contactReduxState.contact]);


    const getContactInfoFromServer = (contactId: string) => {
        dispatch(contactActions.getContactAction({contactId: contactId}));
    }

    const getGroupInfoFromServer = (groupId: string) => {
        dispatch(contactActions.getGroupAction({groupId: groupId}));
    }

    return (
        <>
            {loading && <SpinnerUI/>}
            <NavBar heading={'Contact Manager'}/>
            <LayoutHeading heading={'View Contact'}/>
            {errorMessage && <ErrorMessage message={errorMessage}/>}
            {
                contact && Object.keys(contact).length > 0 && group && Object.keys(group).length > 0 &&
                <ViewContactCard contact={contact} group={group}/>
            }
        </>
    );
}
export default ViewContact;
