import React from 'react';
import {IContactView} from "../models/IContactView";
import {Link} from "react-router-dom";

interface IProps {
    contact: IContactView;
}

const DisplayContactCard: React.FC<IProps> = (props) => {
    const {contact} = props;
    return (
        <>
            <div className="card shadow-lg">
                <div className="card-header text-center">
                    <Link to={`/contacts/view/${contact._id}`}>
                        <img alt="" className="img-fluid w-50 rounded-circle shadow-lg"
                             src={contact.imageUrl}/>
                    </Link>
                </div>
                <div className="card-body">
                    <ul className="list-group">
                        <li className="list-group-item">
                            Name : <span className="fw-bold">{contact.name}</span>
                        </li>
                        <li className="list-group-item">
                            Mobile : <span className="fw-bold">{contact.mobile}</span>
                        </li>
                        <li className="list-group-item">
                            Title : <span className="fw-bold">{contact.title}</span>
                        </li>
                    </ul>
                </div>
            </div>
        </>
    );
}
export default DisplayContactCard;
