import React, {useEffect, useState} from 'react';
import NavBar from "../../../layout/NavBar";
import LayoutHeading from "../../../layout/LayoutHeading";
import {Link, useNavigate, useParams} from "react-router-dom";
import {IContactView} from "../models/IContactView";
import SpinnerUI from "../../../layout/SpinnerUI";
import ErrorMessage from "../../../layout/ErrorMessage";
import * as contactActions from "../../../redux/contacts/contact.actions";
import {AppDispatch, RootState, useAppDispatch} from "../../../redux/store";
import * as contactReducer from "../../../redux/contacts/contact.reducer";
import {useSelector} from "react-redux";

const EditContact: React.FC = () => {
    const {contactId} = useParams();
    const navigate = useNavigate();
    const dispatch: AppDispatch = useAppDispatch();

    const [contactLocal, setContactLocal] = useState<IContactView>({
        name: "",
        company: "",
        title: "",
        mobile: "",
        email: "",
        groupId: "",
        imageUrl: ""
    });

    // get contact State from redux
    const contactReduxState: contactReducer.InitialState = useSelector((state: RootState) => {
        return state[contactReducer.contactFeatureKey];
    })

    const {loading, groups, contact: contactRedux, errorMessage} = contactReduxState;

    useEffect(() => {
        getAllGroupsFromServer();
    }, [])

    useEffect(() => {
        if (contactId) {
            getContactInfoFromServer(contactId);
        }
    }, [contactId])

    useEffect(() => {
        if (contactRedux && Object.keys(contactRedux).length > 0) {
            setContactLocal({
                ...contactLocal,
                name: contactRedux.name,
                company: contactRedux.company,
                title: contactRedux.title,
                mobile: contactRedux.mobile,
                email: contactRedux.email,
                groupId: contactRedux.groupId,
                imageUrl: contactRedux.imageUrl,
            })
        }
    }, [contactRedux]);

    const getContactInfoFromServer = (contactId: string) => {
        dispatch(contactActions.getContactAction({contactId: contactId}));
    }

    const getAllGroupsFromServer = () => {
        dispatch(contactActions.getAllGroupsAction());
    };

    const changeInput = (event: React.ChangeEvent<HTMLInputElement | HTMLSelectElement | any>) => {
        setContactLocal({
            ...contactLocal,
            [event.target.name]: event.target.value
        })
    };

    const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        if (contactId) {
            dispatch(contactActions.updateContactAction({
                contact: contactLocal,
                contactId: contactId
            })).then((response: any) => {
                if (response && !response.error) {
                    navigate('/contacts/admin');
                }
            });
        }
    };

    return (
        <>
            {loading && <SpinnerUI/>}
            <NavBar heading={'Contact Manager'}/>
            <LayoutHeading heading={'Edit Contact'}/>
            {errorMessage && <ErrorMessage message={errorMessage}/>}
            {/*  <pre>{JSON.stringify(contactLocal)}</pre>*/}
            <section className="mt-3">
                <div className="container">
                    <div className="row">
                        <div className="col-sm-4">
                            <form onSubmit={e => handleSubmit(e)}>
                                <div className="mb-2">
                                    <input
                                        value={contactLocal.name}
                                        onChange={e => changeInput(e)}
                                        name={'name'}
                                        className="form-control" id="name-input" placeholder="Name" required
                                        type="text"/>
                                </div>
                                <div className="mb-2">
                                    <input
                                        value={contactLocal.imageUrl}
                                        onChange={e => changeInput(e)}
                                        name={'imageUrl'}
                                        className="form-control" id="image-input" placeholder="Image Url" required
                                        type="text"/>
                                </div>
                                <div className="mb-2">
                                    <input
                                        value={contactLocal.mobile}
                                        onChange={e => changeInput(e)}
                                        name={'mobile'}
                                        className="form-control" id="mobile-input" placeholder="Mobile" required
                                        type="number"/>
                                </div>
                                <div className="mb-2">
                                    <input
                                        value={contactLocal.email}
                                        onChange={e => changeInput(e)}
                                        name={'email'}
                                        className="form-control" id="email-input" placeholder="Email" required
                                        type="email"/>
                                </div>
                                <div className="mb-2">
                                    <input
                                        value={contactLocal.company}
                                        onChange={e => changeInput(e)}
                                        name={'company'}
                                        className="form-control" id="company-input" placeholder="Company" required
                                        type="text"/>
                                </div>
                                <div className="mb-2">
                                    <input
                                        value={contactLocal.title}
                                        onChange={e => changeInput(e)}
                                        name={'title'}
                                        className="form-control" id="title-input" placeholder="Title" required
                                        type="text"/>
                                </div>
                                <div className="mb-2">
                                    <select
                                        value={contactLocal.groupId}
                                        onChange={e => changeInput(e)}
                                        name={'groupId'}
                                        className="form-control" id="group-select-input" required>
                                        <option value="">Select a Group</option>
                                        {
                                            groups.map((group) => {
                                                return (
                                                    <option value={group._id}>{group.name}</option>
                                                )
                                            })
                                        }
                                    </select>
                                </div>
                                <div className="mb-2">
                                    <input className="btn btn-success" type="submit" value="Update"/>
                                    <Link className="btn btn-dark" to="/contacts/admin">Cancel</Link>
                                </div>
                            </form>
                        </div>
                        <div className="col-sm-3">
                            <img alt="" className="img-fluid rounded-circle" id="display-image"
                                 src={contactLocal.imageUrl}/>
                        </div>
                    </div>
                </div>
            </section>
        </>
    );
}
export default EditContact;
