import React, {useEffect, useState} from 'react';
import NavBar from "../../../layout/NavBar";
import LayoutHeading from "../../../layout/LayoutHeading";
import {Link, useNavigate} from "react-router-dom";
import {IContactView} from "../models/IContactView";
import * as contactActions from "../../../redux/contacts/contact.actions";
import {AppDispatch, RootState, useAppDispatch} from "../../../redux/store";
import * as contactReducer from "../../../redux/contacts/contact.reducer";
import {useSelector} from "react-redux";
import SpinnerUI from "../../../layout/SpinnerUI";
import ErrorMessage from "../../../layout/ErrorMessage";

const AddContact: React.FC = () => {
    const navigate = useNavigate();
    const dispatch: AppDispatch = useAppDispatch();

    const [contact, setContact] = useState<IContactView>({
        name: "",
        company: "",
        title: "",
        mobile: "",
        email: "",
        groupId: "",
        imageUrl: ""
    });

    // get contact State from redux
    const contactReduxState: contactReducer.InitialState = useSelector((state: RootState) => {
        return state[contactReducer.contactFeatureKey];
    })

    const {loading, errorMessage, groups} = contactReduxState;

    useEffect(() => {
        dispatch(contactActions.getAllGroupsAction());
    }, []);

    const updateInput = (event: React.ChangeEvent<HTMLInputElement | HTMLSelectElement | any>): void => {
        setContact({
            ...contact,
            [event.target.name]: event.target.value
        })
    };

    const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        dispatch(contactActions.createContactAction({contact: contact})).then((response: any) => {
            if (response && !response.error) {
                navigate('/contacts/admin');
            }
        });
    };

    return (
        <>
            {loading && <SpinnerUI/>}
            <NavBar heading={'Contact Manager'}/>
            <LayoutHeading heading={'Add Contact'}/>
            {errorMessage && <ErrorMessage message={errorMessage}/>}
            {/* <pre>{JSON.stringify(contact)}</pre>*/}
            <section className="mt-3">
                <div className="container">
                    <div className="row">
                        <div className="col-sm-4">
                            <form onSubmit={e => handleSubmit(e)}>
                                <div className="mb-2">
                                    <input
                                        value={contact.name}
                                        name={'name'}
                                        onChange={e => updateInput(e)}
                                        className="form-control" id="name-input" placeholder="Name" required
                                        type="text"/>
                                </div>
                                <div className="mb-2">
                                    <input
                                        value={contact.imageUrl}
                                        name={'imageUrl'}
                                        onChange={e => updateInput(e)}
                                        className="form-control" id="image-input" placeholder="Image Url" required
                                        type="text"/>
                                </div>
                                <div className="mb-2">
                                    <input
                                        value={contact.mobile}
                                        name={'mobile'}
                                        onChange={e => updateInput(e)}
                                        className="form-control" id="mobile-input" placeholder="Mobile" required
                                        type="number"/>
                                </div>
                                <div className="mb-2">
                                    <input
                                        value={contact.email}
                                        name={'email'}
                                        onChange={e => updateInput(e)}
                                        className="form-control" id="email-input" placeholder="Email" required
                                        type="email"/>
                                </div>
                                <div className="mb-2">
                                    <input
                                        value={contact.company}
                                        name={'company'}
                                        onChange={e => updateInput(e)}
                                        className="form-control" id="company-input" placeholder="Company" required
                                        type="text"/>
                                </div>
                                <div className="mb-2">
                                    <input
                                        value={contact.title}
                                        name={'title'}
                                        onChange={e => updateInput(e)}
                                        className="form-control" id="title-input" placeholder="Title" required
                                        type="text"/>
                                </div>
                                <div className="mb-2">
                                    <select
                                        value={contact.groupId}
                                        name={'groupId'}
                                        onChange={e => updateInput(e)}
                                        className="form-control" id="group-select-input" required>
                                        <option value="">Select a Group</option>
                                        {
                                            groups.map((group) => {
                                                return (
                                                    <option value={group._id}>{group.name}</option>
                                                )
                                            })
                                        }
                                    </select>
                                </div>
                                <div className="mb-2">
                                    <input className="btn btn-success" type="submit" value="Create"/>
                                    <Link className="btn btn-dark" to="/contacts/admin">Cancel</Link>
                                </div>
                            </form>
                        </div>
                        <div className="col-sm-3">
                            <img alt="" className="img-fluid rounded-circle" id="display-image"
                                 src={contact.imageUrl}/>
                        </div>
                    </div>
                </div>
            </section>
        </>
    );
}
export default AddContact;
