import axios from "axios";
import {IUserView} from "../models/IUserView";
import {IUser} from "../../../../server/database/models/IUser";

export class UserService {
    private static serverUrl: string = process.env.REACT_APP_EXPRESS_SERVER_URL ? process.env.REACT_APP_EXPRESS_SERVER_URL : "";

    /**
     @usage : Register User
     @method : POST
     @body : username , email , password
     @access : PUBLIC
     @url : http://localhost:9999/users/register
     */
    public static registerUser(user: IUserView): Promise<{ data: { msg: string, user: IUser } }> {
        const dataUrl = `${this.serverUrl}/users/register`;
        return axios.post(dataUrl, user);
    };

    /**
     @usage : Login User
     @method : POST
     @body : email , password
     @access : PUBLIC
     @url : http://localhost:9999/users/login
     */
    public static loginUser(user: IUserView): Promise<{ data: { msg: string, token: string, user: IUser } }> {
        const dataUrl = `${this.serverUrl}/users/login`;
        return axios.post(dataUrl, user);
    };

    /**
     @usage : Get User Info
     @method : GET
     @body : no-body
     @access : PRIVATE
     @url : http://localhost:9999/users/me
     */
    public static getUserInfo(): Promise<{ data: { user: IUser } }> {
        const dataUrl = `${this.serverUrl}/users/me`;
        return axios.get(dataUrl);
    };
}