import {IUserView} from "../../modules/users/models/IUserView";
import {createSlice, isRejectedWithValue} from "@reduxjs/toolkit";
import * as userActions from "../users/user.actions";
import {TokenUtil} from "../../util/TokenUtil";
import {ToastUtil} from "../../util/ToastUtil";

export const userFeatureKey = "userFeature";

export interface InitialState {
    loading: boolean;
    errorMessage: string | null;
    user: IUserView;
    token: string | null;
    isAuthenticated: boolean;
}

const initialState: InitialState = {
    loading: false,
    errorMessage: null,
    user: {} as IUserView,
    token: null,
    isAuthenticated: false
};

export const userSlice = createSlice({
    name: 'userSlice',
    initialState: initialState,
    reducers: {
        logOutUserAction: (state, action) => {
            state.user = {} as IUserView;
            state.token = null;
            state.isAuthenticated = false;
            TokenUtil.removeTokenFromSession();
            ToastUtil.displayInfoMessage("LogOff is Success!");
        }
    },
    extraReducers: (builder) => {
        // registerUserAction
        builder.addCase(userActions.registerUserAction.pending, (state) => {
            state.loading = true;
        }).addCase(userActions.registerUserAction.fulfilled, (state, action) => {
            state.loading = false;
            ToastUtil.displaySuccessMessage("Registration is Success");
        }).addCase(userActions.registerUserAction.rejected, (state, action) => {
            state.loading = false;
            if (isRejectedWithValue(action)) {
                state.errorMessage = action.payload;
                ToastUtil.displayErrorMessage("Registration is Failed");
            }
        })

        // loginUserAction
        builder.addCase(userActions.loginUserAction.pending, (state) => {
            state.loading = true;
        }).addCase(userActions.loginUserAction.fulfilled, (state, action) => {
            state.loading = false;
            if (action.payload.token) {
                state.user = action.payload.user;
                state.token = action.payload.token;
                state.isAuthenticated = true;
                TokenUtil.saveTokenToSession(action.payload.token);
                ToastUtil.displaySuccessMessage("Login is Success");
            }
        }).addCase(userActions.loginUserAction.rejected, (state, action) => {
            state.loading = false;
            if (isRejectedWithValue(action)) {
                state.user = {} as IUserView;
                state.token = null;
                state.isAuthenticated = false;
                TokenUtil.removeTokenFromSession();
                state.errorMessage = action.payload;
                ToastUtil.displayErrorMessage("Login is Failed");
            }
        })

        // getUserInfoAction
        builder.addCase(userActions.getUserInfoAction.pending, (state) => {
            state.loading = true;
        }).addCase(userActions.getUserInfoAction.fulfilled, (state, action) => {
            state.loading = false;
            state.user = action.payload.user;
            state.isAuthenticated = true;
        }).addCase(userActions.getUserInfoAction.rejected, (state, action) => {
            state.loading = false;
            state.user = {} as IUserView;
            state.isAuthenticated = false;
            ToastUtil.displayErrorMessage("Unable to get User Data");
            if (isRejectedWithValue(action)) {
                state.errorMessage = action.payload;
            }
        })
    }
});
export const {logOutUserAction} = userSlice.actions;