import React, {useState} from 'react';
import NavBar from "../../../layout/NavBar";
import LayoutHeading from "../../../layout/LayoutHeading";
import {Link, useNavigate} from "react-router-dom";
import {AppDispatch, RootState, useAppDispatch} from "../../../redux/store";
import * as userReducer from "../../../redux/users/user.reducer";
import {useSelector} from "react-redux";
import {IUserView} from "../models/IUserView";
import * as userActions from "../../../redux/users/user.actions";
import SpinnerUI from "../../../layout/SpinnerUI";

const UserLogin: React.FC = () => {

    const dispatch: AppDispatch = useAppDispatch();
    const navigate = useNavigate();

    // get user data from redux
    const userState: userReducer.InitialState = useSelector((state: RootState) => {
        return state[userReducer.userFeatureKey];
    });

    const {loading} = userState;

    const [user, setUser] = useState<IUserView>({
        username: "",
        email: "",
        password: ""
    });

    const updateInput = (event: React.ChangeEvent<HTMLInputElement>) => {
        setUser({
            ...user,
            [event.target.name]: event.target.value
        })
    };

    const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        dispatch(userActions.loginUserAction({user: user})).then((response: any) => {
            if (response && !response.error) {
                navigate('/');
            }
        });
    };

    return (
        <>
            {loading && <SpinnerUI/>}
            <NavBar heading={'Contact Manager'}/>
            <LayoutHeading heading={'Login User'}/>
            <div className="container">
                <div className="row">
                    <form onSubmit={e => handleSubmit(e)}>
                        <div className="col-sm-4">
                            <div className="mb-2">
                                <input
                                    required={true}
                                    name={'email'}
                                    value={user.email}
                                    onChange={e => updateInput(e)}
                                    type="email" className="form-control" placeholder={'Email'}/>
                            </div>
                            <div className="mb-2">
                                <input
                                    required={true}
                                    name={'password'}
                                    value={user.password}
                                    onChange={e => updateInput(e)}
                                    type="password" className="form-control" placeholder={'Password'}/>
                            </div>
                            <div className="mb-2">
                                <input type="submit" className="btn btn-success" value={'Login'}/>
                                <Link to={'/'} className="btn btn-dark">Cancel</Link>
                            </div>
                            <small>
                                Don't have an account ? <Link to={'/users/register'}
                                                              className="text-decoration-none text-success fw-bold">Register</Link>
                            </small>
                        </div>
                    </form>
                </div>
            </div>
        </>
    );
}
export default UserLogin;
