import axios from "axios";
import {IContactView} from "../models/IContactView";
import {IGroupView} from "../models/IGroupView";

export class ContactService {
    private static serverUrl: string = process.env.REACT_APP_EXPRESS_SERVER_URL ? process.env.REACT_APP_EXPRESS_SERVER_URL : "";

    /**
     @usage : to get all contacts
     @method : GET
     @body : no-params
     @url : http://localhost:9999/contacts
     */
    public static getAllContacts(): Promise<{ data: IContactView[] }> {
        const dataUrl = `${this.serverUrl}/contacts`;
        return axios.get(dataUrl);
    };

    /**
     @usage : get a contact
     @method : GET
     @body : no-params
     @url : http://localhost:9999/contacts/:contactId
     */
    public static getContact(contactId: string): Promise<{ data: IContactView }> {
        const dataUrl = `${this.serverUrl}/contacts/${contactId}`;
        return axios.get(dataUrl);
    };

    /**
     @usage : create a contact
     @method : POST
     @body : name, imageUrl, email, mobile, company, title, groupId
     @url : http://localhost:9999/contacts/
     */
    public static createContact(contact: IContactView): Promise<{ data: IContactView }> {
        const dataUrl = `${this.serverUrl}/contacts/`;
        return axios.post(dataUrl, contact);
    };

    /**
     @usage : Update a contact
     @method : PUT
     @body : name, imageUrl, email, mobile, company, title, groupId
     @url : http://localhost:9999/contacts/:contactId
     */
    public static updateContact(contact: IContactView, contactId: string): Promise<{ data: IContactView }> {
        const dataUrl = `${this.serverUrl}/contacts/${contactId}`;
        return axios.put(dataUrl, contact);
    };

    /**
     @usage : Delete a contact
     @method : DELETE
     @body : no-params
     @url : http://localhost:9999/contacts/:contactId
     */
    public static deleteContact(contactId: string): Promise<{ data: {} }> {
        const dataUrl = `${this.serverUrl}/contacts/${contactId}`;
        return axios.delete(dataUrl);
    };

    /**
     @usage : Get all groups
     @method : GET
     @body : no-params
     @url : http://localhost:9999/groups/
     */
    public static getAllGroups(): Promise<{ data: IGroupView[] }> {
        const dataUrl = `${this.serverUrl}/groups/`;
        return axios.get(dataUrl);
    };

    /**
     @usage : Get a group
     @method : GET
     @body : no-params
     @url : http://localhost:9999/groups/:groupId
     */
    public static getGroup(groupId: string): Promise<{ data: IGroupView }> {
        const dataUrl = `${this.serverUrl}/groups/${groupId}`;
        return axios.get(dataUrl);
    };

}