import React, {useEffect} from 'react';
import NavBar from "../../../layout/NavBar";
import LayoutHeading from "../../../layout/LayoutHeading";
import {Link} from "react-router-dom";
import SpinnerUI from "../../../layout/SpinnerUI";
import ErrorMessage from "../../../layout/ErrorMessage";
import * as contactActions from "../../../redux/contacts/contact.actions";
import {AppDispatch, RootState, useAppDispatch} from "../../../redux/store";
import * as contactReducer from "../../../redux/contacts/contact.reducer";
import {useSelector} from "react-redux";
import * as userReducer from "../../../redux/users/user.reducer";

const AdminContacts: React.FC = () => {
    const dispatch: AppDispatch = useAppDispatch();

    // get contact State from redux
    const contactReduxState: contactReducer.InitialState = useSelector((state: RootState) => {
        return state[contactReducer.contactFeatureKey];
    })

    // get user data from redux
    const userReduxState: userReducer.InitialState = useSelector((state: RootState) => {
        return state[userReducer.userFeatureKey];
    });

    const {user} = userReduxState;

    useEffect(() => {
        getAllContactsFromServer();
    }, [])

    const getAllContactsFromServer = () => {
        dispatch(contactActions.getAllContactsAction());
    };

    const clickDeleteContact = (contactId: string | undefined) => {
        if (contactId) {
            dispatch(contactActions.deleteContactAction({contactId: contactId})).then((response: any) => {
                if (response && !response.error) {
                    getAllContactsFromServer();
                }
            });
        }
    };

    const {loading, errorMessage, contacts} = contactReduxState;

    const displayAdminContent = () => {
        return <div className="container">
            <div className="row">
                <div className="col">
                    <Link className="btn btn-success mt-2" to="/contacts/add">
                        <i className="bi bi-plus-circle"></i> New </Link>
                    <table className="table table-hover text-center table-striped mt-3">
                        <thead className="bg-dark text-white">
                        <tr>
                            <th>SNO</th>
                            <th>Image</th>
                            <th>Name</th>
                            <th>Mobile</th>
                            <th>Title</th>
                            <th>Company</th>
                            <th>Action</th>
                        </tr>
                        </thead>
                        <tbody>
                        {
                            !loading && contacts && contacts.length > 0 && contacts.map((contact, index) => {
                                return (
                                    <tr key={contact._id}>
                                        <td>{index + 1}</td>
                                        <td>
                                            <img src={contact.imageUrl} alt="" width={50} height={50}/>
                                        </td>
                                        <td>{contact.name}</td>
                                        <td>{contact.mobile}</td>
                                        <td>{contact.title}</td>
                                        <td>{contact.company}</td>
                                        <td>
                                            <Link to={`/contacts/edit/${contact._id}`}>
                                                <button className="btn btn-primary">
                                                    <i className="bi bi-pencil"></i>
                                                </button>
                                            </Link>
                                            <button onClick={() => clickDeleteContact(contact._id)}
                                                    className="btn btn-danger">
                                                <i className="bi bi-trash"></i>
                                            </button>
                                        </td>

                                    </tr>
                                )
                            })
                        }
                        {
                            !loading && contacts.length === 0 &&
                            <tr>
                                <td colSpan={7} className="text-danger">------ No Contacts Found ------</td>
                            </tr>
                        }
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    }

    return (
        <>
            {loading && <SpinnerUI/>}
            <NavBar heading={'Contact Manager'}/>
            <LayoutHeading heading={'Contacts Admin'}/>
            {errorMessage && <ErrorMessage message={errorMessage}/>}
            {user && user.isAdmin ? displayAdminContent() : <div className="container">
                <div className="row">
                    <div className="col text-center">
                        <p className="h4 text-danger">------ You are not Authorized to access this page ------</p>
                    </div>
                </div>
            </div>}


        </>
    );
}
export default AdminContacts;
