import React from 'react';
import {Link} from "react-router-dom";
import {TokenUtil} from "../../../util/TokenUtil";

const HomePage: React.FC = () => {

    return (
        <>
            <div className="landing-page">
                <div className="wrapper">
                    <div className="d-flex justify-content-center align-items-center text-center h-100 flex-column">
                        <p className="display-1 text-light">React Contact Mgr</p>
                        <div>
                            {
                                TokenUtil.isLoggedIn() ?
                                    <Link className="btn btn-warning" to="/contacts/display">View Contacts</Link> :
                                    <Link className="btn btn-success" to="/users/login">Login</Link>
                            }
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}
export default HomePage;
