import {IContactView} from "../../modules/contacts/models/IContactView";
import {IGroupView} from "../../modules/contacts/models/IGroupView";
import {createSlice, isRejectedWithValue} from "@reduxjs/toolkit";
import * as contactActions from "./contact.actions";
import {ToastUtil} from "../../util/ToastUtil";

export const contactFeatureKey = "contactFeature";

export interface InitialState {
    loading: boolean;
    errorMessage: string | null;
    contacts: IContactView[];
    contact: IContactView;
    groups: IGroupView[];
    group: IGroupView;
}

const initialState: InitialState = {
    loading: false,
    errorMessage: null,
    contacts: [] as IContactView[],
    contact: {} as IContactView,
    groups: [] as IGroupView[],
    group: {} as IGroupView
}

export const contactSlice = createSlice({
    name: 'contactSlice',
    initialState: initialState,
    reducers: {},
    extraReducers: (builder) => {
        // getAllContactsAction
        builder.addCase(contactActions.getAllContactsAction.pending, (state) => {
            state.loading = true;
        }).addCase(contactActions.getAllContactsAction.fulfilled, (state, action) => {
            state.loading = false;
            state.contacts = action.payload;
        }).addCase(contactActions.getAllContactsAction.rejected, (state, action) => {
            state.loading = false;
            ToastUtil.displayErrorMessage("Unable to get the contacts");
            if (isRejectedWithValue(action)) {
                state.errorMessage = action.payload.message
            }
        })

        // getContactAction
        builder.addCase(contactActions.getContactAction.pending, (state) => {
            state.loading = true;
        }).addCase(contactActions.getContactAction.fulfilled, (state, action) => {
            state.loading = false;
            state.contact = action.payload;
        }).addCase(contactActions.getContactAction.rejected, (state, action) => {
            state.loading = false;
            ToastUtil.displayErrorMessage("Unable to get the contact");
            if (isRejectedWithValue(action)) {
                state.errorMessage = action.payload.message
            }
        })

        // createContactAction
        builder.addCase(contactActions.createContactAction.pending, (state) => {
            state.loading = true;
        }).addCase(contactActions.createContactAction.fulfilled, (state, action) => {
            state.loading = false;
            state.contact = action.payload;
            ToastUtil.displaySuccessMessage("Contact is Created!");
        }).addCase(contactActions.createContactAction.rejected, (state, action) => {
            state.loading = false;
            ToastUtil.displayErrorMessage("Contact Creation Failed!");
            if (isRejectedWithValue(action)) {
                state.errorMessage = action.payload.message
            }
        })

        // updateContactAction
        builder.addCase(contactActions.updateContactAction.pending, (state) => {
            state.loading = true;
        }).addCase(contactActions.updateContactAction.fulfilled, (state, action) => {
            state.loading = false;
            state.contact = action.payload;
            ToastUtil.displaySuccessMessage("Contact is Updated!");
        }).addCase(contactActions.updateContactAction.rejected, (state, action) => {
            state.loading = false;
            ToastUtil.displayErrorMessage("Contact is Update is Failed!");
            if (isRejectedWithValue(action)) {
                state.errorMessage = action.payload.message
            }
        })

        // deleteContactAction
        builder.addCase(contactActions.deleteContactAction.pending, (state) => {
            state.loading = true;
        }).addCase(contactActions.deleteContactAction.fulfilled, (state) => {
            state.loading = false;
            state.contact = {} as IContactView;
            ToastUtil.displayInfoMessage("Contact is Deleted!");
        }).addCase(contactActions.deleteContactAction.rejected, (state, action) => {
            state.loading = false;
            ToastUtil.displayErrorMessage("Contact is Delete is Failed!");
            if (isRejectedWithValue(action)) {
                state.errorMessage = action.payload.message
            }
        })

        // getAllGroupsAction
        builder.addCase(contactActions.getAllGroupsAction.pending, (state) => {
            state.loading = true;
        }).addCase(contactActions.getAllGroupsAction.fulfilled, (state, action) => {
            state.loading = false;
            state.groups = action.payload;
        }).addCase(contactActions.getAllGroupsAction.rejected, (state, action) => {
            state.loading = false;
            ToastUtil.displayErrorMessage("Unable to get all the groups!");
            if (isRejectedWithValue(action)) {
                state.errorMessage = action.payload.message
            }
        })

        // getGroupAction
        builder.addCase(contactActions.getGroupAction.pending, (state) => {
            state.loading = true;
        }).addCase(contactActions.getGroupAction.fulfilled, (state, action) => {
            state.loading = false;
            state.group = action.payload;
        }).addCase(contactActions.getGroupAction.rejected, (state, action) => {
            state.loading = false;
            ToastUtil.displayErrorMessage("Unable to get the group!");
            if (isRejectedWithValue(action)) {
                state.errorMessage = action.payload.message
            }
        })
    }
})
















