import React from 'react';
import {ToastContainer} from "react-toastify";

const ReactToastifyConfiguration: React.FC = () => {
    return (
        <>
            <ToastContainer
                position="top-left"
                autoClose={3000}
                hideProgressBar={false}
                newestOnTop={false}
                closeOnClick
                rtl={false}
                pauseOnFocusLoss
                draggable
                pauseOnHover
                theme="colored"
            />
        </>
    );
}
export default ReactToastifyConfiguration;
